import React from 'react'
import ReactDOM from 'react-dom'
import { Provider } from 'react-redux'

import './config'
import App from './App'
import { store } from './store'
import './globalStyles'

ReactDOM.render(
  <React.StrictMode>
    <Provider store={store}>
      <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);